//
/// Brand colors
$default-color: #4d4d4d !default;
$primary-color: #29abe2 !default;
$secondary-color: #1f80aa !default;
$accent-color: #ffc142 !default;

//
// State colors
$success-color: #0e927a !default;
$error-color: #a65261 !default;
$warning-color: #ffc142 !default;
$info-color: #1f80aa !default;

//
/// Greys
$grey-light-1:   #adadad;
$grey-light-2:   #e6e6e6;
$grey-light-3:   #f5f5f5;
$grey-dark-1:    #4d4d4d;
$grey-dark-2:    #666666;


$default-color-dark: mix(black, $default-color, 30%) !default;
$default-color-light: mix(white, $default-color, 30%) !default;
$default-color-bright: mix(white, $default-color, 60%) !default;

$primary-color-dark: mix(black, $primary-color, 30%) !default;
$primary-color-light: mix(white, $primary-color, 30%) !default;
$primary-color-bright: mix(white, $primary-color, 60%) !default;

$secondary-color-dark: mix(black, $secondary-color, 30%) !default;
$secondary-color-light: mix(white, $secondary-color, 30%) !default;
$secondary-color-bright: mix(white, $secondary-color, 60%) !default;

$accent-color-dark: mix(black, $accent-color, 30%) !default;
$accent-color-light: mix(white, $accent-color, 30%) !default;
$accent-color-bright: mix(white, $accent-color, 60%) !default;





/*** Badges ***/
$badge-bg-color: $primary-color-dark !default;

/*** Buttons ***/
$border-radius-base: 10px;

// Shared styles
$button-border: 0 !default;
$button-font-size: 0.875rem !default;
$button-height: 36px !default;
$button-padding: 0 1em !default;
$button-radius: 10px !default;

// Disabled styles
$button-disabled-background: #DFDFDF !default;
$button-disabled-color: #9F9F9F !default;

// Raised buttons
$button-raised-background: $primary-color;
$button-raised-background-hover: $primary-color-light !default;
$button-raised-color: white !default;

// Large buttons
$button-large-font-size: 1em;

// Flat buttons
$button-flat-color: #343434 !default;
$button-flat-disabled-color: lighten(#999, 10%) !default;

// Floating buttons
$button-floating-size: 44px !default;
$button-floating-large-size: $button-floating-size + 12px !default;
$button-floating-radius: 50% !default;

/*** Dropdown ***/
$dropdown-bg-color: #fff !default;
$dropdown-hover-bg-color: #eee !default;
$dropdown-color: $primary-color-dark !default;
$dropdown-item-height: 50px !default;

/*** Collapsible ***/
$collapsible-height: 3rem !default;
$collapsible-header-color: #fff !default;
$collapsible-border-color: #ddd !default;

/*** Date Picker ***/
$datepicker-weekday-bg: darken($primary-color-dark, 7%) !default;
$datepicker-date-bg: $primary-color-dark !default;
$datepicker-year: rgba(255, 255, 255, .4) !default;
$datepicker-focus: rgba(0,0,0, .05) !default;
$datepicker-selected: $primary-color-dark !default;
$datepicker-selected-outfocus: desaturate(lighten($primary-color-dark, 35%), 15%) !default;

/*** Forms ***/
// Text Inputs + Textarea
$input-border-color: #9e9e9e !default;
$input-bg-color: white !default;
$input-error-color: $error-color !default;
$input-success-color: $success-color !default;
$input-focus-color: $success-color !default;
$label-font-size: .8em !default;
$input-disabled-color: rgba(0,0,0, .26) !default;
$input-disabled-solid-color: #BDBDBD !default;

$radio-fill-color: $primary-color-dark !default;
$radio-empty-color: #5a5a5a !default;
$radio-border: 2px solid $radio-fill-color !default;

// Switches
$switch-bg-color: $primary-color-dark !default;
$switch-checked-lever-bg: desaturate(lighten($primary-color-dark, 25%), 25%) !default;
$switch-unchecked-bg: #F1F1F1 !default;
$switch-unchecked-lever-bg: #818181 !default;


$gutter-width: 1.5rem !default;
$element-top-margin: $gutter-width/3 !default;
$element-bottom-margin: ($gutter-width*2)/3 !default;

// Media queries ================================================================
$x-small: 480px !default;
$small: 768px !default;
$medium: 960px !default;
$large: 1024px !default;
$x-large: 1200px !default;
$xx-large: 1440px !default;

// Grid ==========================================================================

$num-cols: 12 !default;
$gutter-width: 30px !default;
$element-top-margin: $gutter-width/3 !default;
$element-bottom-margin: ($gutter-width*2)/3 !default;

// Typography =====================================================================

$off-black: rgba(0, 0, 0, 0.87) !default;
// Header Styles

// Cards ==========================================================================

$card-padding: 24px !default;
$card-padding-large: 48px !default;
$card-radius: 10px !default;
$card-bg-color: white !default;
$card-link-color: $primary-color !default;
$card-link-color-light: lighten($card-link-color, 20%) !default;


// Tables ==========================================================================
$table-border-color: #d0d0d0 !default;
$table-striped-color: #f2f2f2 !default;
